.Youtube{
    article{
        border-top: 1px solid #aaa;
        padding: 20px 0px;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        h3{
            width: 44%;
            font-weight: bold;
            font-size: 22px;
            font-family: 'work sans';
            color: #777;
        }
        .txt{
            width: 38%;
            display: flex;
            flex-wrap: wrap;
            align-content: space-between;
            p{
                font: 14px/1.2 'arial';
                color: #999;
                filter: saturate(0);
            }
            span{
                font: 12px/1 'arial';
                color: orange;
            }
        }
        .pic{
            width: 40%;
            height: 200px;
            background: #000;
            filter: saturate(0.3);
            transition: 0.5s;
            img{
                width: 100%;
                height: 100%;
                object-fit: cover;

            }
            &:hover{
                filter: saturate(1.2);
            }
        }
    }
}

@media screen and (max-width:$tablet) {
    .Youtube{
        article{
            h3{width: 100%; margin-bottom: 20px;}
            .txt{width: 100%;}
            .pic{height: 150px;
            margin-bottom: 0px;}
        }
    }
    
}